import tTable from '@/components/t-table/t-table.vue'
import tTh from '@/components/t-table/t-th.vue'
import tTr from '@/components/t-table/t-tr.vue'
import tTd from '@/components/t-table/t-td.vue'
var timer_s = getNowFormatDate() + " 00:00:00"
var timer_e = getNowFormatDate() + " 23:59:59"

function getNowFormatDate() {
	var date = new Date();
	var seperator1 = "-";
	var seperator2 = ":";
	var month = date.getMonth() + 1;
	var strDate = date.getDate();
	if (month >= 1 && month <= 9) {
		month = "0" + month;
	}
	if (strDate >= 0 && strDate <= 9) {
		strDate = "0" + strDate;
	}
	var currentdate = date.getFullYear() + seperator1 + month + seperator1 +
		strDate;
	return currentdate;
}
export default {
	components: {
		tTable,
		tTh,
		tTr,
		tTd,
	},
	data() {
		return {
			huohao:'',
			worktype:'',
			color:'',
			chuanghao:'',
			chima:'',
			userid:'',  //编号
			tableList: [],
			uid: '',
			title: '',
			curr_worktype_bh: '',
			curr_worktype_name: '',
			value1: '',
			value2: '',
			zs: '0',
			sl1: '0',
			sl2:'0',        //包数
			dk: ''
		}
	},
	onBackPress(options) {
		if (options.from === 'backbutton') {
			uni.switchTab({
				url: '/pages/index/index'
			});
			return true;
		}
		return false;
	},
	onLoad(r) {
		let temp = JSON.stringify(r);
		if(temp.indexOf('.html') < 0 && JSON.stringify(r) !='{}'){  
				this.huohao = r.huohao;
				this.chuanghao = r.chuanghao;
				this.color = r.color
				this.chima = r.chima
				this.userid = r.userid
				this.worktype = r.worktype
		}
		this.dk = this.websiteUrl,
		uni.getStorage({
			key: 'fwqsj',
			success: res => {
				this.dk = res.data
				console.log(this.dk);
			}
		}),
		uni.getStorage({
			key: 'loginData',
			success: res => {
				this.title = res.data.uname
				this.uid = res.data.uid
				this.curr_worktype_bh = res.data.curr_worktype_bh
				
				uni.request({
					url: `https://${this.dk}/gongziMx.aspx`,
					method: 'GET',
					data: {
						uid:this.uid,
						dates:timer_s,
						datee:timer_e,
						huohao:this.huohao,
						worktype:this.worktype,
						color:this.color,
						chuanghao:this.chuanghao,
						chima:this.chima,
						userid:this.userid
					},
					success: res => {
						console.log(res.data);
						this.tableList = res.data
						let temp = res.data;
						let sum = 0;
						let sl1 = 0;
						for (let i = 0; i < temp.length; i++) {
							sum = sum + temp[i].je
							sl1 = sl1 + temp[i].sl
						}
						this.zs = sum.toFixed(2);
						this.sl1 = sl1;
						this.sl2 = temp.length;   //包数
						this.huohao = ''
					}
				})
			}
		})		
	},
	methods: {
		onpeDetailedMore(){
			uni.navigateTo({
				url:'../info/info1?flag=1'
			})
		},
		onpeDetailed() {
			uni.request({
				url: `https://${this.dk}/gongziMx.aspx`,
				method: 'GET',
				data: {
					uid:this.uid,
					dates:timer_s,
					datee:timer_e,
					huohao:this.huohao,
					worktype:this.worktype
				},
				success: res => {
					this.tableList = res.data
					let temp = res.data;
					let sum = 0
					let sl1 = 0;
					for (let i = 0; i < temp.length; i++) {
						sum = sum + temp[i].je
						sl1 = sl1 + temp[i].sl
					}
					this.zs = sum.toFixed(2);
					this.sl1 = sl1;
					this.sl2 = temp.length;   //包数
					this.huohao = ''
				}
			});
		}
	}
}
